.app {
  text-align: center;
  padding: 2rem;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
  max-width: 1200px; /* Limit the width to ensure cards fit on screen */
  margin: 0 auto; /* Center the card container */
}

.card {
  width: calc(25% - 0.5rem); /* Adjust card width to fit 4 cards per row */
  cursor: pointer;
  transition: opacity 0.3s ease-in-out;
  user-select: none;
}

.card img {
  max-width: 100%;
  height: auto;
}

.card.dragging {
  opacity: 0.7;
}

.drop-zone {
  clear: both;
  margin-top: 2rem;
  border: 2px dashed #aaa;
  padding: 2rem;
  text-align: center;
}

/* Add these styles to your existing App.css or create a new CSS file */

.navbar {
  background-color: #1a1a1a;
  padding: 1rem 0;
  text-align: center;
}

.nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.nav-item {
  margin: 0 1rem;
}

.nav-link {
  text-decoration: none;
  color: #ffffff;
  font-size: 1.2rem;
  transition: color 0.3s ease;
}

.nav-link:hover {
  color: #ffd700;
}

/* Update your CSS file with the following styles */

.card-list {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.card {
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  background-color: #fff;
  cursor: pointer;
  transition: transform 0.3s;
}

.card.small {
  width: 100px;
}

.card img {
  max-width: 100%;
  height: auto;
}

.card:hover {
  transform: scale(1.05);
}

/* Update your CSS file with the following styles */

.content {
  width: 80%;
  max-width: 1200px;
  padding: 40px;
  background-color: #fff;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

.card.small {
  padding: 10px;
}

.card img {
  max-width: 100%;
  height: auto;
}

.card p {
  margin: 10px 0 0;
  text-align: center;
  font-weight: bold;
}

.card:hover {
  transform: scale(1.05);
}
